const Index = resolve => require(['@/views/index/index.vue'], resolve);
const welcomeIndex = resolve => require(['@/views/index/overview.vue'], resolve);
const addressComponent = resolve => require(['@/views/welcome/address.vue'], resolve);
const equityRegister = resolve => require(['@/views/welcome/equityRegister.vue'], resolve);
const registerRecord = resolve => require(['@/views/welcome/registerRecord.vue'], resolve);
const noTransferComponent = resolve => require(['@/views/welcome/noTransfer.vue'], resolve);
const turnRecord = resolve => require(['@/views/welcome/turnRecord.vue'], resolve);
const turnInRecord = resolve => require(['@/views/welcome/turnInRecord.vue'], resolve);

export default {
  path: "/",
  component: Index,
  name: "Index",
  redirect:'/welcome',
  children : [
    {
      path: '/welcome',
      component: welcomeIndex,
      name: "股权交易"
    },
    {
      path: '/welcome/address',
      component: addressComponent,
      name: "我的账户地址",
    },
    {
      path: '/welcome/equityRegister',
      component: equityRegister,
      name: "股权登记",
    },
    {
      path: '/welcome/registerRecord',
      component: registerRecord,
      name: "登记记录",
    },
    {
      path: '/welcome/noTransfer',
      component: noTransferComponent,
      name: "非交易过户",
    },
    {
      path: '/welcome/turnRecord',
      component: turnRecord,
      name: "转出记录",
    },
    {
      path: '/welcome/turnInRecord',
      component: turnInRecord,
      name: "转入记录",
    }
  ]
}


